
body, html, #app {
  width: 100%;
  height: 100%;
}
.index {
  width: 100%;
  height: 100%;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  background-image: url(../../static/images/1.jpg);
}
.index .entry-box {
    position: fixed;
    bottom: 0;
    width: 100%;
    padding: 50px 20px;
}
.index .entry-box a {
      height: 40px;
      background: rgba(255, 255, 255, 0.5);
      line-height: 40px;
      margin: 5px;
}



