
body, html, #app {
  width: 100%;
  height: 100%;
}
.hitokoto {
  width: 100%;
  height: 100%;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  background-image: url(../../static/images/background.jpg);
}
.hitokoto .content {
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.5);
    position: relative;
}
.hitokoto .content .content-text {
      position: absolute;
      width: 55%;
      top: 50%;
      left: 50%;
      -webkit-transform: translateX(-50%) translateY(-70%);
              transform: translateX(-50%) translateY(-70%);
      color: #fff;
}
.hitokoto .content .content-text .main-text {
        font-size: 26px;
}
.hitokoto .content .content-text .txt-left {
        text-align: left;
}
.hitokoto .content .content-text .txt-center {
        padding: 0 5%;
}
.hitokoto .content .content-text .txt-right {
        text-align: right;
}
.hitokoto .content .content-text .author {
        color: rgba(255, 255, 255, 0.87);
        font-size: 20px;
        text-align: right;
        margin-top: 10px;
}
.hitokoto .entry-box {
    position: fixed;
    bottom: 0;
    width: 100%;
    padding: 50px 20px;
}
.hitokoto .entry-box a {
      height: 40px;
      background: rgba(255, 255, 255, 0.5);
      line-height: 40px;
      margin: 5px;
}
@media screen and (max-width: 600px) {
.hitokoto .content .content-text {
    width: 95%;
}
}



